<template>
    <div class="map">
        <e-vue-esrimapjs v-if="baseMapType === 'tdt'" :mapType="mapTypeConfig[baseMapType].mapType" :mapUrl="mapTypeConfig[baseMapType].mapUrl" :submapUrl="mapTypeConfig[baseMapType].submapUrl" :geoUrl="prop.geoUrl" :initExtent="newInitExtent" :gisApiUrl="prop.gisApiUrl" v-on:baseLayerChange="onBaseLayerChange($event)" v-on:mapReady="onMapReady($event)"></e-vue-esrimapjs>
        <e-vue-esrimapjs v-if="baseMapType === 'google'" :mapType="mapTypeConfig[baseMapType].mapType" :mapUrl="mapTypeConfig[baseMapType].mapUrl" :submapUrl="mapTypeConfig[baseMapType].submapUrl" :geoUrl="prop.geoUrl" :initExtent="newInitExtent" :gisApiUrl="prop.gisApiUrl" v-on:baseLayerChange="onBaseLayerChange($event)" v-on:mapReady="onMapReady($event)"></e-vue-esrimapjs>
    </div>

</template>

<script>
export default {
  name: "e-map",
  props: {
    prop: {
      type: Object,
      default: function() {
        return {
          geoUrl:
            " http://tasks.arcgisonline.com/ArcGIS/rest/services/Geometry/GeometryServer",
          gisApiUrl: "http://js.arcgis.com/3.23/",
          initExtent: {
            xmax: 116.39029888900006,
            xmin: 116.04209077900009,
            ymax: 40.161018230000025,
            ymin: 39.885287565000056
          },
          baseMapType: "tdt"
        };
      }
    }
  },
  data() {
    return {
      newInitExtent: this.prop.initExtent,
      mapTypeConfig: {
        tdt: {
          mapType: "tdt",
          mapUrl: ["vec", "cva"],
          submapUrl: [["img", "cia"], ["ter", "cta"]]
        },
        google: {
          mapType: "google",
          mapUrl: "m",
          submapUrl: ["y", "p"]
        }
      },
      baseMapType: this.prop.baseMapType,
      mapComponent: "",
      map: ""
    };
  },
  methods: {
    onMapReady(event) {
      this.mapComponent = event;
      this.map = this.mapComponent.map;
    },
    onBaseLayerChange() {}
  },
  watch: {
    "prop.initExtent": {
      handler(newName, oldName) {
        if (this.mapComponent) {
          this.mapComponent.setExtent(JSON.parse(newName));
        }
      },
      deep: true
    },
    "prop.baseMapType": {
      handler(newVal) {
        this.baseMapType = newVal;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.map {
  height: 100%;
}
.e-vue-esrimapjs {
  height: 100%;
}
</style>

